* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: Lato, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.player-score {
    text-align: center;
    font-size: 80px;
    font-weight: 100;
    color: #EB4D4D;
    margin-bottom: 130px;
}


.player-name {
    font-size: 40px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 100;
    margin-top: 20px;
    margin-bottom: 10px;
}

.player-current-box {
    background-color: #EB4D4D;
    color: #fff;
    width: 50%;
    margin: 0 auto;
    padding: 12px;
    text-align: center;
}

.player-current-label {
    text-transform: uppercase;
    margin-bottom: 10px;
    font-size: 12px;
    color: #222;
}

.player-current-score {
    font-size: 30px;
}
img {
    grid-area: dice;
    width: 50%;
    height: auto;
    box-shadow: 0.5px 0.5px 15px 0.5px rgb(180, 180, 180);
}
.players {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 2fr 1fr 2fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "player-1 menu player-2";
}

.player1 {
    grid-area: player-1;
    width: 100%;
    height: 100%;
    justify-self: center;
    align-self: center;
    text-align: center;
}

.menu {
    display: grid;
    grid-area: menu;
    height: 500px;
    justify-self: center;
    align-self: center;
    text-align: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 2fr 1fr 1fr;
    grid-template-areas: 
    "new"
    "input"
    "dice"
    "roll"
    "hold"
}

.menu .new {
    grid-area: new;
    align-self: flex-start;
}

.menu .roll {
    grid-area: roll;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-self: center;
}

.menu .hold {
    grid-area: hold;
}

.player2 {
    grid-area: player-2;
    width: 100%;
    height: 100%;
    justify-self: center;
    align-self: center;
    text-align: center;
}

.btn {
    width: 200px;
    left: 50%;
    color: #555;
    background: none;
    border: none;
    font-family: Lato;
    font-size: 20px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: 300;
}

.menu .default {
    grid-area: input;
    justify-items: center;
}

.menu input {
    justify-self: center;
    width: 200px;
    height: 30px;
    padding:0;
}

.active { background-color: #f7f7f7; }

.active .player-name { font-weight: 300; }

.active .player-name::after {
    content: "\2022";
    font-size: 47px;
    color: #EB4D4D;
    margin-left: 10px;  
}

.winner { 
    background-color: #f7f7f7; 
}

.winner .player-name { 
    font-weight: 300; 
    color: #EB4D4D; 
}
.App {
  display: flex;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(
      top,
      rgba(62, 20, 20, 0.45) 0%,
      rgba(62, 20, 20, 0.45) 100%
    ),
    url(/static/media/back.d0402957.jpg) center;
  background: linear-gradient(
      to bottom,
      rgba(62, 20, 20, 0.45) 0%,
      rgba(62, 20, 20, 0.45) 100%
    ),
    url(/static/media/back.d0402957.jpg) center;
  background-size: cover;
  justify-content: center;
  align-items: center;
}

.containerBox {
  background-color: white;
  width: 80%;
  height: 550px;
  box-shadow: +2px +5px 10px 0px black;
}

